import { Component, OnInit, TemplateRef } from '@angular/core';
import { HttpService } from '../http.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { Employee } from '../model/employee';
import { DialogService } from '../dialog.service';
import { ToastrService  } from 'ngx-toastr';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  p:number=1;
  nameSearch:string='';
  empdata:any=[];
  isRadioChecked:boolean=false;
  empObj=<Employee>{};
 
  modalRef: BsModalRef; 
  
  config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-danger"
  };


 // myUsername:string='';

  constructor(private service:HttpService, 
    private modalservice:BsModalService,
    private dialogservice:DialogService,
    private toastr:ToastrService) { }

  ngOnInit() {
    this.GetDataFromBackend();
  //  this.myUsername=sessionStorage.getItem("username")
  }

  GetDataFromBackend(){
    this.service.getEmployee().subscribe((respose)=>{
      this.empdata=(<any>respose);
      console.log(this.empdata); 
    })
  }

  onRadio(item){
    this.isRadioChecked=true;
    console.log(item);
    this.empObj=item;
  }

  radioCheck(){
    if(this.isRadioChecked)
      return true;
    else
      return false;
  }

  onUpdate(popup: TemplateRef<any>){
    if(this.radioCheck()){
      this.modalRef = this.modalservice.show(        
        popup, this.config);
    }
    else
      alert("Please select any one record to update.....!");
  }

  onDelete(){
    if(this.radioCheck()){

      this.dialogservice.OpenConfirmDialog('Are you sure to delete this record?')
      .afterClosed().subscribe(res=>{
        if(res){
          this.service.DeleteRecord(this.empObj.id)
          .subscribe(response=>{
            // console.log(response);
            this.toastr.success('Deleted Successfully!!! ');
            this.GetDataFromBackend();
          })
        }
        
      })
    }
    else{
      // alert("Please select record to delete......!");
      this.toastr.warning("Please select record to delete......!");
    }
  }

  onStatusUpdate(){
    if(this.radioCheck()){
      this.service.updateStatus(this.empObj)
      .subscribe(response=>{
        this.toastr.success(response)
        this.GetDataFromBackend();
      })
    }
    else{
      this.toastr.warning("Please select record to update status......!");
    }
  }

}
