import { Component, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpService } from '../http.service';
import { Employee } from '../model/employee';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-employeedetails',
  templateUrl: './employeedetails.component.html',
  styleUrls: ['./employeedetails.component.css']
})
export class EmployeedetailsComponent implements OnInit {

  constructor(private route:ActivatedRoute,private service:HttpService,
    private modalservice:BsModalService) { }

   empobj=<Employee>{};
   modalRef: BsModalRef;

   config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-danger"
  };

  ngOnInit() {
    this.getDataFromUrl();
  }

  getDataFromUrl(){
    this.route.paramMap.subscribe((param)=>{
      // console.log(this.myId);
      this.getDataFromBackend(param.get("id"));
    })
  }

  getDataFromBackend(eid){
    this.service.getEmployeeById(eid).subscribe((response)=>{
      // console.log(response);
      this.empobj=(<Employee>response);
    })
  }

  onUpdate(popup: TemplateRef<any>){
    this.modalRef = this.modalservice.show(        
      popup, this.config);
  }

}
